import React from 'react';
import { Doughnut } from 'react-chartjs-2';

const data = {
	labels: [
		'Red',
		'Gray',
	],
	datasets: [{
		data: [80, 20],
		backgroundColor: [
		'#FF6384',
		'#C9C9C9',
		],
		hoverBackgroundColor: [
		'#FF6384',
		'#A1A1A1',
		]
	}]
};

const DoughnutExample = () => {
  return (
    <React.Fragment>
      <Doughnut data={data} options={{ responsive: true }} />
    </React.Fragment>
  )
}

export default DoughnutExample
